$primary-color: #225173;
$secondary-color: #fdfdfd;
$tertiary-color: #dddddd;

// 12 column layout calculator
// @args [1-12]
// i.e: left: column-position(2)

@function column-position($column) {
	@return ($column / 12 * 100%); }

@mixin breakpoint($class) {
	@if ($class == large) {
		@media (max-width: 1250px) {
			@content; } }
	@if ($class == medium) {
		@media (max-width: 950px) {
			@content; } }
	@if ($class == small) {
		@media (max-width: 600px) {
			@content; } } }

body {
	font-family: 'Montserrat', sans-serif;
	line-height: 2.5em; }
h1, h2, h3, p, li, figure, body {
	margin: 0; }
ul {
	margin: 0; }
.hero {
	position: relative;
	background: url('img/candlelight.jpg') no-repeat;
	background-size: cover;
	color: $secondary-color;
	height: 35vw;
	@include breakpoint(medium) {
		height: 30vw;
		min-height: 350px; }
	&__title {
		position: absolute;
		display: flex;
		top: 5%;
		left: column-position(2);
		h1 {
			padding-top: 1.25em; }
		figure img {
			height: 100px;
			float: left; } }
	&__info {
		position: absolute;
		display: flex;
		@include breakpoint(medium) {
			flex-wrap: wrap; }
		justify-content: space-around;
		bottom: 5%;
		left: 0px;
		width: 100%;
		&__schedule {
			float: left;
			display: flex;
			@include breakpoint(medium);
			&__sessions {
				text-align: right;
				margin-right: 1em; }
			&__times {
				font-weight: bold; } }
		&__address {
			float: left; } } }
.mission_statement {
	background: $primary-color;
	width: 100%;
	min-height: 15em;
	blockquote {
		padding: 5% 0;
		margin: auto;
		margin-top: 0;
		width: 60%;
		color: $secondary-color;
		text-align: center; } }
.pastor {
	background: $secondary-color;
	min-height: 350px;
	padding: 5% column-position(1);
	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		&__portrait {
			float: left;
			margin-right: 2em;
			img {
				max-height: 500px; }
			@include breakpoint(large) {
				margin-right: 0; } }
		&__description {
			position: relative;
			float: left;
			line-height: 1.75em;
			text-indent: 2em;
			width: column-position(6);
			@include breakpoint(large) {
				width: 100%; } } }
	&__title {
		text-align: center; } }
.calendar {
	min-height: 500px;
	width: 100%;
	padding: 5% 0;
	background: $tertiary-color;
	&__embed {
		margin: auto; } }
.footer {
	background: $primary-color;
	color: $secondary-color;
	padding: 2em 0;
	min-height: 200px;
	justify-content: center;
	div {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		@include breakpoint(medium) {
			justify-content: flex-start; } }
	section {
		margin: 1.5em column-position(1)/2;
		@include breakpoint(medium) {
			width: 40%; }
		@include breakpoint(small) {
			width: 100%; }
		div {
			ul {
				list-style-type: none;
				padding: 0;
				line-height: 1.5em;
				white-space: nowrap; } } }
	&__contact_us, &__hours {
		li {
			&:nth-child(odd) {
				font-weight: bold; } } } }
